
export default {
    state: {
      cartItems: [],
    },
    mutations: {
      addToCart(state, book) {
        state.cartItems.push(book);
      },
      removeFromCart(state, bookId) {
        state.cartItems = state.cartItems.filter(item => item.id !== bookId);
      },
      clearCart(state) {
        state.cartItems = [];
      },
    },
    actions: {
      
    },
   
  };
