<template>
  <div class="col-md-12">
     <!-- Container for the login card -->
    <div class="card card-container">
      <img
        id="profile-img"
        src="//ssl.gstatic.com/accounts/ui/avatar_2x.png"
        class="profile-img-card"
      />
       <!-- Form group for username -->
      <form name="form" @submit.prevent="handleLogin">
        <div class="form-group">
          <label for="username">Username</label>
          <input
            v-model="user.username"
            v-validate="'required'"
            type="text"
            class="form-control"
            name="username"
          />
           <!-- Error message for username -->
          <div
            v-if="errors.has('username')"
            class="alert alert-danger"
            role="alert"
          >Username is required!</div>
        </div>
        <!-- Form group for password -->
        <div class="form-group">
          <label for="password">Password</label>
          <input
            v-model="user.password"
            v-validate="'required'"
            type="password"
            class="form-control"
            name="password"
          />
          <!-- Error message for password -->
          <div
            v-if="errors.has('password')"
            class="alert alert-danger"
            role="alert"
          >Password is required!</div>
        </div>
        <!-- Login button -->
        <div class="form-group">
          <button class="btn btn-primary btn-block" :disabled="loading">
            <span v-show="loading" class="spinner-border spinner-border-sm"></span>
            <span>Login</span>
          </button>
        </div>
        <!-- Google login button -->
        <div class="form-group">
          <button class="btn btn-danger btn-block" :disabled="loading" @click="googleLogin">
            <span v-show="loading" class="spinner-border spinner-border-sm"></span>
            <span>Login with Google</span>
          </button>
        </div>
        <!-- Display error message if login fails -->
        <div class="form-group">
          <div v-if="message" class="alert alert-danger" role="alert">{{message}}</div>
        </div>
        <!-- Signup link and Github login button -->
        <b-form-group class="text-center">
          <p>
            Don't have an account? You can
            <router-link to="/signup">create one by Github </router-link>
          </p>
          <b-button class="social-button" @click="socialGithubLogin">
            <img src="../assets/github_logo.png" width="20"/>
          </b-button>
        </b-form-group>
      </form>
    </div>
  </div>
</template>

<script>
import User from '../models/user';

export default {
  name: 'Login',
  data() {
    return {
      user: new User('', ''),
      loading: false,
      message: '',
      email: "",
      password: "",
      username: "",
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    }
  },
  created() {
    if (this.loggedIn) {
      this.$router.push('/profile');
    }
  },
  methods: {
    handleLogin() {
      this.loading = true;
      this.$validator.validateAll().then(isValid => {
        if (!isValid) {
          this.loading = false;
          return;
        }

        if (this.user.username && this.user.password) {
          this.$store.dispatch('auth/login', this.user).then(
            () => {
              this.$router.push('/profile');
            },
            error => {
              this.loading = false;
              this.message =
                (error.response && error.response.data && error.response.data.message) ||
                error.message ||
                error.toString();
            }
          );
        }
      });
    },
    async googleLogin() {
      try {
        const response = await this.$auth.loginWith('google');
        // successfully
        console.log(response);
      } catch (error) {
        // login error
        console.error(error);
      }
    },
  }
};
</script>

<style scoped>
label {
  display: block;
  margin-top: 10px;
}

.card-container.card {
  max-width: 350px !important;
  padding: 40px 40px;
}

.card {
  background-color: #f7f7f7;
  padding: 20px 25px 30px;
  margin: 0 auto 25px;
  margin-top: 50px;
  -moz-border-radius: 2px;
  -webkit-border-radius: 2px;
  border-radius: 2px;
  -moz-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
}

.profile-img-card {
  width: 96px;
  height: 96px;
  margin: 0 auto 10px;
  display: block;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  object-fit: cover;
}

.form-control {
  font-size: 16px;
}

.btn-primary, .btn-danger {
  background-color: #3498db;
  color: #fff;
  border: none;
}

.btn-primary:hover, .btn-danger:hover {
  background-color: #2980b9;
}

.alert-danger {
  color: #a94442;
  background-color: #f2dede;
  border-color: #ebccd1;
}

.social-button {
  background-color: #333;
  color: #fff;
  border: none;
  margin-top: 10px;
}

.social-button:hover {
  background-color: #555;
}
</style>
