<template>
  <div class="container">
    <!-- Search Section -->
    <div class="search-section">
      <div class="col-md-8">
        <div class="input-group mb-3" style="background-color: rgba(255, 255, 255, 0.8);">
          <input
            type="text"
            class="form-control"
            placeholder="Search by title"
            v-model="searchTitle"
            @keyup.enter="searchBooks"
          />
          <!-- Search button triggers book retrieval -->
          <div class="input-group-append">
            <button
                class="btn btn-outline-secondary"
                type="button"
                @click="page = 1; retrieveBooks();"
            >
              Search
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- Book Grid Section -->
    <div class="book-grid-section" style="background-color: rgba(255, 255, 255, 0.8);backdrop-filter: blur(10px);">
      <v-col cols="12" sm="12">
        <v-card class="mx-auto" tile>
          <v-card-title class="text-primary">Books</v-card-title>

          <!-- Grid Section -->
          <v-row>
            <v-col v-for="book in books" :key="book.id" cols="12" md="6" lg="4">
              <!-- Wrap v-img inside router-link -->
              <router-link :to="{ name: 'book-details', params: { id: book.id } }">
               <v-card class="mb-3" max-width="400" height="100%" style="background-color: transparent;">
                     <v-responsive aspect-ratio="1.5">
                    <v-img :src="book.img" contain></v-img>
                    </v-responsive>
                 <v-card-title class="text-h6">{{ book.title }}</v-card-title>
               </v-card>
              </router-link>
            </v-col>
          </v-row>
        </v-card>
      </v-col>
    </div>

    <!-- Pagination Section -->
    <div class="pagination-section">
      <div class="col-md-12">
        <b-pagination
          v-model="page"
          :total-rows="count"
          :per-page="pageSize"
          prev-text="Prev"
          next-text="Next"
          @change="handlePageChange"
        ></b-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import UserService from '../services/user.service';
import BookDataService from "@/services/BookDataService";

export default {
  name: 'Admin',
  data() {
     // Initial data properties for the component.
    return {
      content: '',
      books: [],
      title: "",
      searchTitle: "",
      headers: [
        {text: "ID", sortable: false, value: "id"},
        {text: "Title", align: "start", sortable: false, value: "title"},
        {text: "Author", value: "author", sortable: false},
        {text: "Status", value: "status", sortable: false},
        {text: "Actions", value: "actions", sortable: false},
      ],
      totalPages: 0,
      currentPage: 1,
      page: 1,
      count: 0,
      pageSize: 25,
      pageSizes: [3, 6, 9],
    };
  },
  methods: {
    // Helper method to prepare request parameters for the book search.
    getRequestParams(searchTitle, page, pageSize) {
      let params = {};

      if (searchTitle) {
        params["title"] = searchTitle;
      }

      if (page) {
        params["page"] = page - 1;
      }

      if (pageSize) {
        params["size"] = pageSize;
      }

      return params;
    },
    // Method to handle the book search functionality.
    searchBooks() {
      this.page = 1;
      this.retrieveBooks();
    },
    // Method to retrieve books based on search criteria and pagination.
    retrieveBooks() {
      const params = this.getRequestParams(
          this.searchTitle,
          this.page,
          this.pageSize
      );

      BookDataService.getAll(params)
          .then((response) => {
            const { books, totalItems } = response.data;
            this.books = books;
            this.count = totalItems;

            console.log(response.data);
          })
          .catch((e) => {
            console.log(e);
          });
    },
    // Method to handle when the page size selection changes.
    handlePageSizeChange(event) {
      this.pageSize = event.target.value;
      this.page = 1;
      this.retrieveBooks();
    },
    // Method to handle when the current page changes.

    handlePageChange(value) {
      this.page = value;
      this.retrieveBooks();
    },
    // Method to refresh the list of books
    refreshList() {
      this.retrieveBooks();
    },
    editBook(id) {
      // Method to navigate to the book editing page
      this.$router.push({ name: "book-details", params: { id: id } });
    },
    // Method to delete a book from the list
    deleteBook(id) {
      BookDataService.delete(id)
          .then(() => {
            this.refreshList();
          })
          .catch((e) => {
            console.log(e);
          });
    },
    // Method to handle changes in pagination
    onPageChange(page) {
      this.retrieveBooks(page);
    },
  },
  // Lifecycle hook for component creation
  mounted() {
    UserService.getAdminBoard().then(
        response => {
          this.content = response.data;
        },
        error => {
          this.content =
              (error.response && error.response.data && error.response.data.message) ||
              error.message ||
              error.toString();
        }
    );
    // Initial retrieval of books to populate the list.
    this.retrieveBooks();
  }
};
</script>

<style>
.container {
  //background-image: url("~@/assets/background.gif");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  min-height: 100vh; 
  display: flex;
  flex-direction: column;
}

.search-section,
.book-grid-section,
.pagination-section {

  padding: 20px;
  margin: 10px;
  border-radius: 10px; 
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); 
}

.search-section input,
.search-section button,
.book-grid-section,
.pagination-section {
  background-color: transparent; 
}
.search-section input {
  width: 100%; 
}

</style>