<template>
  <div class="cart-container">
    <h2 style="color: black; font-weight: bold;">Shopping Cart</h2>
     <!-- Table for displaying cart items -->
    <table>
      <thead>
        <tr>
          <th style="color: black; font-weight: bold;">Title</th>
          <th style="color: black; font-weight: bold;">Price</th>
          <th style="color: black; font-weight: bold;">Action</th>
        </tr>
      </thead>
      <tbody>
        <!-- Loop through each item in the cart and display them as table rows -->
        <tr v-for="item in cartItems" :key="item.id">
          <td style="color: black;">{{ item.title }}</td>
          <td style="color: black;">{{ item.price }}</td>
          <td>
            <button @click="removeFromCart(item.id)" class="action-button">Remove</button>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- Display total price if there are items in the cart -->
    <p v-if="showTotal" style="color: black; font-weight: bold;">Total Price: {{ totalPrice }}</p>
    <!-- Button to clear all items from the cart -->
    <button @click="clearCart" class="clear-cart-button">Clear Cart</button>

     <div class="payment-section">
      <h2 style="color: black; font-weight: bold; border-bottom: 2px solid black; padding-bottom: 10px;">Payment Information</h2>

      <div class="payment-inputs">
        <div class="input-group" style="background-color: #333;">
          <label for="cardNumber">Card Number:</label>
          <input type="text" id="cardNumber" v-model="cardNumber" required>
        </div>

        <div class="input-group" style="background-color: #333;">
          <label for="expirationMonth">MM:</label>
          <input type="text" id="expirationMonth" v-model="expirationMonth" maxlength="2" required>
        </div>

        <div class="input-group" style="background-color: #333;">
          <label for="expirationYear">YY:</label>
          <input type="text" id="expirationYear" v-model="expirationYear" maxlength="2" required>
        </div>

        <div class="input-group" style="background-color: #333;">
          <label for="cvv">CVV:</label>
          <input type="text" id="cvv" v-model="cvv" maxlength="3" required>
        </div>
      </div>
    </div>


    <button @click="checkoutAndStore" class="checkout-button">Checkout and Store to Collection</button>
  </div>

</template>

<script>
import BookDataService from "../services/BookDataService";

export default {
  computed: {
    // Retrieves cart items from the Vuex store
    cartItems() {
      return this.$store.state.cart.cartItems;
    },
    showTotal() {
      // Check if there are items in the cart before showing the total price
      return this.cartItems.length > 0;
    },
    totalPrice() {
      // Calculate total price based on the items in the cart
      return this.cartItems.reduce((total, item) => total + item.price, 0);
    },
    currentUser() {
      return this.$store.state.auth.user;
    },
    showAdminBoard() {
      if (this.currentUser && this.currentUser.roles) {
        return this.currentUser.roles.includes('ROLE_ADMIN');
      }
      return false;
    }
  },
  data() {
    return {
      showPaymentForm: false,
      cardNumber: '',
      cvv: '',
      // Add data properties for other payment details
    };
  },
  methods: {
    removeFromCart(bookId) {
      this.$store.commit("removeFromCart", bookId);
    },
    clearCart() {
      this.$store.commit("clearCart");
    },
   checkoutAndStore() {
      
      this.cartItems.forEach((item) => {
        this.buyBook(item.id);
      });

      
      // this.$router.push('/payment');
    },
    buyBook(bookId) {
      var data = {
        userid: this.currentUser.id,
        bookid: bookId,
      };
      BookDataService.buybook(data)
        .then((response) => {
          console.log(response.data);
          if (response.data.status === "OK") {
            this.$router.push('/profile');
          }
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },
};
</script>

<style scoped>
.cart-container {
  //background-image: url('~@/assets/cart.gif');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  padding: 20px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

th, td {
  border: 1px solid black;
  padding: 8px;
  text-align: left;
}

.action-button {
  color: black;
  font-weight: bold;
  background-color: #3498db;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
}

.clear-cart-button {
  color: black;
  font-weight: bold;
  background-color: #e74c3c;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  margin-top: 10px;
}

.checkout-button {
  color: black;
  font-weight: bold;
  background-color: #2ecc71; 
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  margin-top: 10px;
}
.payment-section {
  margin-top: 20px;
  border: 2px solid black;
  padding: 10px;
  }
.payment-section h2 {
  color: white;
  font-weight: bold;
  border-bottom: 2px solid white;
  padding-bottom: 10px;
}

  .payment-inputs {
    display: flex;
    flex-wrap: wrap;
  }

  .input-group {
    margin-right: 20px;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    background-color:rgba(51, 51, 51, 0.9);
    padding: 10px;
    border-radius: 5px;
  }

  .input-group label {
    color: black;
    margin-right: 5px;
    front-weight: bold;
  }

  .input-group input {
    color: white;
  }

</style>
