import AuthService from '../services/auth.service';

// Retrieve the user from local storage to check if they are logged in
const user = JSON.parse(localStorage.getItem('user'));
const initialState = user
  ? { status: { loggedIn: true }, user }
  : { status: { loggedIn: false }, user: null };

export const auth = {
  namespaced: true,
  state: initialState,
  // Defines various actions for authentication
  actions: {
    // Action to log in a user
    login({ commit }, user) {
      return AuthService.login(user).then(
        user => {
          // On successful login, update the state to reflect the user's logged-in status
          commit('loginSuccess', user);
          return Promise.resolve(user);
        },
        error => {
           // On login failure, update the state to reflect the failure
          commit('loginFailure');
          return Promise.reject(error);
        }
      );
    },
    // Action to log out a user
    logout({ commit }) {
      AuthService.logout();
      // Update the state to reflect the user's logged-out status
      commit('logout');
    },
     // Action to register a new user
    register({ commit }, user) {
      return AuthService.register(user).then(
        response => {
          // On successful registration, update the state
          commit('registerSuccess');
          return Promise.resolve(response.data);
        },
        error => {
          // On registration failure, update the state
          commit('registerFailure');
          return Promise.reject(error);
        }
      );
    }
  },
  // Defines mutations to change the state
  mutations: {
    // Mutation to update state on successful login
    loginSuccess(state, user) {
      state.status.loggedIn = true;
      state.user = user;
    },
    // Mutation to update state on login failure
    loginFailure(state) {
      state.status.loggedIn = false;
      state.user = null;
    },
    // Mutation to update state on logout
    logout(state) {
      state.status.loggedIn = false;
      state.user = null;
    },
    registerSuccess(state) {
      // Mutation to update state on successful registration
      state.status.loggedIn = false;
    },
     // Mutation to update state on registration failure
    registerFailure(state) {
      state.status.loggedIn = false;
    }
  }
};
